import * as React from 'react';
import { Helmet } from 'react-helmet';
import * as  ReactMarkdown from 'react-markdown';
import {
    NavLink,
    Redirect,
    Route,
} from 'react-router-dom';
import ReactSVG from 'react-svg';
import Dataset from '../dataset';
import DownArrowSVG from '../svg/purple/down-arrow.svg';
import GenericNotFound from './GenericNotFound';

const SkillDomain = (props: any) => {
    const match = Dataset.skills[props.match.params.domainId] !== undefined;
    const titles = { software: 'Développement logiciel', wood: 'Transformation du bois' };
    if (!match) { return <GenericNotFound /> };
    const data = Dataset.skills[props.match.params.domainId];
    return (
        <div>
            <Helmet>
                <title>Compétences: {titles[props.match.params.domainId]} | Cyprien Taque</title>
            </Helmet>
            <h3 className="purple regular subTitle">Compétences en {titles[props.match.params.domainId].toLowerCase()}</h3>
            {
                data.map((unit: any) =>
                    (
                        <ul key={unit.id} className="mainList-item">
                            <li className="relative">
                                {unit.icon
                                    ? <div className="technologyIcon">
                                        <img alt="icon" src={unit.icon} />
                                    </div>
                                    : ''
                                }
                                <strong className="purple">{unit.name}</strong> -
                                <span className="grey light">{unit.level ? unit.level : ''}</span>
                                <ul>
                                    {
                                        unit.technologies.map((tech: any) => {
                                            if (tech.name && tech.level && tech.content) {
                                                return (
                                                    <li className="grey marginTop" key={tech.id} >
                                                        {tech.name
                                                            + ' - '
                                                            + tech.level
                                                            + ': '
                                                            + <ReactMarkdown className="inlineBlock" source={tech.content} />
                                                        }
                                                    </li>
                                                );
                                            } else if (tech.name && tech.content) {
                                                return (
                                                    <li className="grey marginTop inlineChilds" key={tech.id} >
                                                        <span className="regular purple">
                                                            {tech.name}: </span>
                                                        <ReactMarkdown source={tech.content} />
                                                    </li>
                                                );
                                            } else if (tech.name && tech.level) {
                                                return (
                                                    <li className="grey marginTop" key={tech.id} >
                                                        <span className="regular purple">{tech.name}</span>
                                                        : {tech.level}</li>
                                                );
                                            } else {
                                                return (
                                                    <li key={tech.id} className="grey marginTop">
                                                        <span className="purple">{tech.name}</span>
                                                    </li>);
                                            }
                                        }
                                        )
                                    }
                                </ul>
                            </li>
                        </ul>
                    )
                )
            }
        </div>
    );
};

const Skills = (props: any) => {
    if (props.match.isExact) {
        return <Redirect to={`${props.match.url}/software`} />
    }
    return (
        <div>
            <Helmet>
                <title>Compétences | Cyprien Taque</title>
            </Helmet>
            <nav className="subNav">
                <h2 className="purple regular">
                    <div className="svgInline">
                        <ReactSVG path={DownArrowSVG}
                            svgStyle={{ width: '20px', height: '20px', marginLeft: '2px' }} />
                    </div>
                    Menu
                </h2>
                <ul>
                    <li>
                        <NavLink className="navlink" to={`${props.match.url}/software`}>
                            Développement logiciel
                        </NavLink>
                    </li>
                    <li>
                        <NavLink className="navlink" to={`${props.match.url}/wood`}>
                            Métiers du bois
                        </NavLink>
                    </li>
                </ul>
            </nav>
            <Route path={`${props.match.url}/:domainId`} component={SkillDomain} />
        </div>
    );
};

export default Skills;




