import * as React from 'react';
import { Helmet } from 'react-helmet';
import {
    NavLink
} from 'react-router-dom';

const Home = (props: any) => (
    <div>
        <Helmet>
            <title>Introduction | Cyprien Taque</title>
        </Helmet>
        <h2 className="regular purple">Développement logiciel</h2>
        <p className="grey">
            Actuellement développeur front-end chez <a rel="noopener noreferrer" target="_blank" href="https://www.cinedigitalservice.com/cds/"> Ciné digital services</a>, je me suis initié au développement web par la création du site <a rel="noopener noreferrer" target="_blank" href="http://ctprods.fr"> CTProds</a>, puis j'ai complété ma formation autodidacte par une  formation diplomante d'un titre de développeur logiciel à l'école IMIE (nov. 2016 - sept. 2018).<br /><br />J'aime les architectures propres et bien pensées, la programmation fonctionnelle et les interfaces design. <a rel="noopener noreferrer" target="_blank" href="https://ant.design/">#antd</a>
        </p>
        <br /><br />
        <h2 className="regular purple">Productique bois et conception 3d</h2>
        <p className="grey">
            Dans une autre vie... <br />
            Mon parcours de formation en <NavLink to="/studies/wood">productique bois </NavLink>suivi d'<NavLink to="/experiences/wood">expériences professionnelles</NavLink> dans ce domaine m'ont apporté une double compétence <strong>manuelle</strong> en atelier de menuiserie et <b>intéllectuelle</b> en conception dans l'industrie. <br /><br />
            J'ai éxercé le métier de technicien (dessinateur, chef de projets en recherche et développement) dans les domaines de la menuiserie industrielle, l'agencement mobilier et la conception de salles de bains en matériaux composites et tôle pliée. Voici quelques-uns des projets que j'ai réalisé dans ce domaine: <a rel="noopener noreferrer" target="_blank" href="http://ctprods.fr/profile/CyprienTaque"> CTProds</a>
        </p>
    </div>
);
export default Home;



