import * as React from 'react';
import {
    NavLink
} from 'react-router-dom';

const DesktopNav = () =>
    (
        <nav className="desktop onlyDesktop">
            <div className="PageWrap --with-gutter">
                <ul>
                    <li><NavLink to="/"
                        exact={true}
                        activeClassName="active"><span>Introduction</span></NavLink></li>
                    <li><NavLink to="/experiences"
                        activeClassName="active"><span>Expériences</span></NavLink></li>
                    <li><NavLink to="/studies"
                        activeClassName="active"><span>Etudes</span></NavLink></li>
                    <li><NavLink to="/skills"
                        activeClassName="active"><span>Compétences</span></NavLink></li>
                    <li><NavLink to="/hobbies"
                        activeClassName="active"><span>Loisirs</span></NavLink></li>
                    <li className="onlyMobile"><NavLink to="/contact"
                        className="navlink"
                        activeClassName="active"><span>Contact</span></NavLink></li>
                </ul>
            </div>
        </nav>
    );


export default DesktopNav;



