import * as React from 'react';
import { Helmet } from 'react-helmet';
import * as ReactMarkdown from 'react-markdown';
import Dataset from '../dataset';

const Hobbies = (props: any) => (
    <div>
        <Helmet>
            <title>Loisirs | Cyprien Taque</title>
        </Helmet>
        <div className="separator-h" />
        <h2 className="onlyMobile purple regular">Loisirs</h2>
        <div className="separator-h onlyMobile" />
        <ul>
            {
                Dataset.hobbies.map((unit: any) =>
                    <li key={unit.id} className="marginTop purple mainList-item">
                        {unit.name}
                        <ul>
                            {
                                unit.content.map((content: any) =>
                                    <li key={content.id} className="marginTop grey">
                                        <ReactMarkdown source={content.content} />
                                    </li>
                                )
                            }
                        </ul>
                    </li>
                )
            }
        </ul>
    </div>
);

export default Hobbies;



