import * as React from 'react';
import { Helmet } from 'react-helmet';
import Moment from 'react-moment';
import { RouteComponentProps } from 'react-router';
import {
    NavLink,
    Redirect,
    Route
} from 'react-router-dom';
import ReactSVG from 'react-svg';
import Dataset from '../dataset';
import DownArrowSVG from '../svg/purple/down-arrow.svg';
import GenericNotFound from './GenericNotFound';

interface IMatchParams {
    domainId: number;
}

interface IProps extends RouteComponentProps<IMatchParams> {
    domain: any;
    titles: any;
}

interface IState {
    match: any;
    filter: string | undefined;
}

class ExperienceDomain extends React.Component<IProps, IState> {

    constructor(props: IProps) {
        super(props);
        this.state = {
            filter: '',
            match: Dataset.experiences[props.match.params.domainId] !== undefined,
        };
    }

    public setFilter(filter: string): void {
        this.setState({
            filter
        });
    }

    public render() {
        const { filter, match } = this.state;

        const titles = { software: 'Développement logiciel', wood: 'Transformation du bois et matériaux composites' };
        const data = Dataset.experiences[this.props.match.params.domainId].filter((value: any) => filter ? value.contract_type === filter : true);

        return (
            !match
                ? <GenericNotFound />
                : <div>
                    <Helmet>
                        <title>Expériences: {titles[this.props.match.params.domainId]} | Cyprien Taque</title>
                    </Helmet>
                    <h3 className="purple regular subTitle">Expériences en {titles[this.props.match.params.domainId].toLowerCase()}</h3>
                    <p className="noMargin">
                        <input name="filter" id="all" onChange={this.setFilter.bind(this, '')} defaultChecked={true} type="radio" value="" />
                        <label htmlFor="all" className="grey block">Tout</label>
                    </p>
                    <p className="noMargin">
                        <input name="filter" id="cdi" onChange={this.setFilter.bind(this, 'travail')} type="radio" value="" />
                        <label htmlFor="cdi" className="grey block">Contrat de travail</label>
                    </p>
                    <p className="noMargin">
                        <input name="filter" id="stage" onChange={this.setFilter.bind(this, 'stage')} type="radio" value="" />
                        <label htmlFor="stage" className="grey block">Stage</label>
                    </p>

                    <div className="timeline filterContainer">
                        {
                            data.map((unit: any) =>
                                <ul key={unit.id} className={'relative experienceUnit mainList-item ' + unit.contract_type}>
                                    <strong className="purple timelineItem">{unit.institution}</strong>
                                    <small className="timelineItem-date">
                                        <Moment parse="MM/YYYY" fromNow={true}>
                                            {unit.beginning}
                                        </Moment>
                                    </small>
                                    {unit.icon
                                        ? <div className="technologyIcon">
                                            <img alt="icon" src={unit.icon} />
                                        </div>
                                        : ''
                                    }
                                    <span className="grey light">- {unit.location}</span>
                                    <span className="regular purple " style={{ display: 'block' }}>{unit.headline}</span>
                                    <ul>
                                        <li className="grey">
                                            <Moment parse="MM/YYYY" format=" MMMM YYYY">
                                                {unit.beginning}
                                            </Moment>
                                            -
                                        {unit.end
                                                ? <Moment parse="MM/YYYY" format=" MMMM YYYY">
                                                    {unit.end}
                                                </Moment>
                                                : ' en cours'
                                            }
                                        </li>
                                        <li className="grey">{unit.tasks}</li>
                                        <li className="grey">
                                            <span className="regular purple">Technologies: </span>
                                            {unit.technologies}</li>
                                        <li className="grey">
                                            <span className="regular purple">Environnement: </span>
                                            {unit.environment}</li>
                                        {/* <li className="grey">Contrat : {unit.contract}</li> */}
                                    </ul>
                                </ul>
                            )
                        }
                    </div>
                </div>
        );
    }
}

const Experiences = (props: any) => {
    if (props.match.isExact) {
        return <Redirect to={`${props.match.url}/software`} />
    }
    return (
        <div>
            <Helmet>
                <title>Expériences | Cyprien Taque</title>
            </Helmet>
            <nav className="subNav">
                <h2 className="purple regular">
                    <div className="svgInline">
                        <ReactSVG path={DownArrowSVG}
                            svgStyle={{ width: '20px', height: '20px', marginLeft: '2px' }} />
                    </div>
                    Menu
                </h2>
                <ul>
                    <li>
                        <NavLink className="navlink" to={`${props.match.url}/software`}>
                            Développement logiciel
                        </NavLink>
                    </li>
                    <li>
                        <NavLink className="navlink" to={`${props.match.url}/wood`}>
                            Transformation du bois et des matériaux composites
                        </NavLink>
                    </li>
                </ul>
            </nav>
            <Route path={`${props.match.url}/:domainId`} component={ExperienceDomain} />
        </div>
    );
};

export default Experiences;



