import * as React from 'react';
import { elastic as Menu } from 'react-burger-menu';
import { NavLink } from 'react-router-dom';
import SidebarStyles from './SidebarStyle';

interface IState {
    isOpen: boolean;
}

export default class Sidebar extends React.Component<{}, IState>{

    constructor(props: any) {
        super(props);
        this.state = { isOpen: false };
        this.handleClick = this.handleClick.bind(this);
    }

    public handleClick() {
        this.setState({ isOpen: false });
    }

    public render() {
        return (
            <Menu right={true}
                isOpen={this.state.isOpen}
                width={300}
                styles={SidebarStyles}
                pageWrapId={'page-wrap'}
                burgerButtonClassName={'onlyMobile'}
                outerContainerId={'outer-container'} >
                <h3 className="">Menu</h3>
                <ul className="sidebar-list">
                    <li><NavLink to="/" exact={true} className="" activeClassName="active">Introduction</NavLink></li>
                    <li><NavLink to="/experiences" className="" activeClassName="active">Expériences</NavLink></li>
                    <li><NavLink to="/studies"
                        className=""
                        onClick={this.handleClick}
                        activeClassName="active">Etudes</NavLink></li>
                    <li><NavLink to="/skills"
                        className=""
                        onClick={this.handleClick}
                        activeClassName="active">Compétences</NavLink></li>
                    <li><NavLink to="/hobbies"
                        className=""
                        onClick={this.handleClick}
                        activeClassName="active">Loisirs</NavLink></li>
                    <li><NavLink to="/contact"
                        onClick={this.handleClick}
                        activeClassName="active">Contact</NavLink></li>
                </ul>
            </Menu>
        );
    }
}



