import * as React from 'react';
import { Helmet } from 'react-helmet';
import Moment from 'react-moment';
import {
    NavLink,
    Redirect,
    Route,
} from 'react-router-dom';
import ReactSVG from 'react-svg';
import Dataset from '../dataset';
import DownArrowSVG from '../svg/purple/down-arrow.svg';
import GenericNotFound from './GenericNotFound';

const StudyDomain = (props: any) => {
    const match = Dataset.studies[props.match.params.domainId] !== undefined;
    if (!match) { return <GenericNotFound /> };
    const titles = { software: 'Développement logiciel', wood: 'Transformation du bois' };
    const data = Dataset.studies[props.match.params.domainId];
    return (
        <div>
            <Helmet>
                <title>Etudes: {titles[props.match.params.domainId]} | Cyprien Taque</title>
            </Helmet>
            <h3 className="purple regular subTitle">Etudes en {titles[props.match.params.domainId].toLowerCase()}</h3>
            {
                data.map((unit: any) =>
                    <ul key={unit.id} className="relative mainList-item">
                        {unit.icon
                            ? <div className="technologyIcon">
                                <img alt="icon" src={unit.icon} />
                            </div>
                            : ''
                        }
                        <strong className="purple">{unit.institution}</strong>
                        - <span className="grey light">{unit.location}
                            - <Moment parse="MM/YYYY" fromNow={true}>
                                {unit.beginning}
                            </Moment>
                        </span>
                        <li className="grey" key={unit.id}>{unit.text}</li>
                        <div className="purple">
                            <Moment parse="MM/YYYY" format=" MMMM YYYY">
                                {unit.beginning}
                            </Moment>
                            -
                         <Moment parse="MM/YYYY" format=" MMMM YYYY">
                                {unit.end}
                            </Moment>
                        </div>
                    </ul>
                )
            }
        </div>
    );
};

const Studies = (props: any) => {
    if (props.match.isExact) {
        return <Redirect to={`${props.match.url}/software`} />
    }
    return (
        <div>
            <Helmet>
                <title>Etudes | Cyprien Taque</title>
            </Helmet>
            <nav className="subNav">
                <h2 className="purple regular">
                    <div className="svgInline">
                        <ReactSVG path={DownArrowSVG}
                            svgStyle={{ width: '20px', height: '20px', marginLeft: '2px' }} />
                    </div>
                    Menu
                </h2>
                <ul>
                    <li>
                        <NavLink className="navlink" to={`${props.match.url}/software`}>
                            Développement logiciel
                        </NavLink>
                    </li>
                    <li>
                        <NavLink className="navlink" to={`${props.match.url}/wood`}>
                            Métiers du bois
                        </NavLink>
                    </li>
                </ul>
            </nav>
            <Route path={`${props.match.url}/:domainId`} component={StudyDomain} />
        </div>
    );
};

export default Studies;



